@charset "utf-8";
*{margin: 0px;padding: 0px;}
body{
  color:#333;
  font-size: 12px;
}
li,ol,ul{
  list-style: none;
}
.box{
  display: flex;
  justify-content: center;
}
.movies{
    display: grid;
    grid-template-columns: repeat(2,minmax(400px,1fr));
    grid-gap: 100px;
    width: 80%;
}
@media screen and (max-width:767px){
  .movies{
    display: grid;
    grid-template-columns: 400px;
    width: 80%;
}
}
.movie{
  background-color: #fff;
  box-shadow: 0 13px 27px -5px rgb(50 50 93 / 25%), 0 8px 16px -8px rgb(0 0 0 / 30%), 0 -6px 16px -6px rgb(0 0 0 / 3%);
  padding: 10px;
  line-height: 30px;
}
.poster{
  width: 150px;
}
.movie h3{
  font-size: 24px;
}
.year{
  font-size: 16px;
}
.genre{
  display: flex;
  font-size: 20px;
}
.genre li{
  margin: 0px 10px;
  color:#5F00FF;
}
.summary{
  font-size: 16px;
  color:#999;
}